*{font-size:14px;font-family:Nunito;box-sizing:border-box}body{background-color:#f8f8fa;margin:0px}h1,h2,h3,h4,h5,h6,p{margin:0}@font-face{font-family:"Nunito";src:url("../../font/Nourito/Nunito-Regular.ttf") format("truetype");font-weight:normal;font-style:normal}@font-face{font-family:"Nunito";src:url("../../font/Nourito/Nunito-SemiBold.ttf") format("truetype");font-weight:600;font-style:normal}.header{background:#fff;padding:48px 108px;display:flex;justify-content:space-between;align-items:center}.header .container{display:flex;align-items:center;max-width:595px;width:595px}.header .container .checkbox-component:nth-child(2){margin-left:24px}.header .container .checkbox-component:nth-child(3){margin-left:32px}.header .button{width:88px}@media screen and (max-width: 768px){.header{flex-direction:column}}.button{position:relative;background-color:#4460f7;border-radius:4px;cursor:pointer;color:#fff;border:none;height:38px;width:100%;transition:background-color .3s ease}.button:hover{background-color:#2140e8}.button:disabled{background-color:#9194a5}.outline{border:1px solid #4460f7;background-color:#fff;color:#4460f7}.outline:hover{background-color:inherit;border:1px solid #2140e8;color:#2140e8}.outline:disabled{background-color:inherit;border:1px solid #9194a5;color:#9194a5}.loader{border:4px solid #e0e2ea;border-top:4px solid #4460f7;border-radius:50%;height:56px;width:56px;animation:spin 2s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.image{width:100%}.productCard{background-color:#fff;border-radius:8px;overflow:hidden;display:flex;flex-direction:column}.productCard .content{display:flex;flex-direction:column;justify-content:space-between;padding:16px 24px 16px;height:100%}.productCard .content .actions{margin-top:auto}.productCard .content .actions .button{margin-top:16px}.productCardTitle{font-size:18px;margin-bottom:8px;color:#1a1b1d}.productCardDescription{font-weight:600;color:#9194a5}.rating{display:flex;padding: